<template>
  <div class="H15-1216-500">
    <div class="operate">
      <el-form ref="form" :inline="true" :model="searchForm" class="demo-form-inline">
        <el-form-item label="CX" prop="CX">
          <el-select v-model="searchForm.CX" filterable placeholder="CX" clearable>
            <el-option
              v-for="(item, index) in CX_OPTION"
              :key="index"
              :label="item"
              :value="item">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="SFZCKMC" prop="SFZCKMC">
          <el-select v-model="searchForm.SFZCKMC" filterable placeholder="SFZCKMC" clearable>
            <el-option
              v-for="(item, index) in SFZCKMC_OPTION"
              :key="index"
              :label="item"
              :value="item">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="SFZRKMC" prop="SFZRKMC">
          <el-select v-model="searchForm.SFZRKMC" filterable placeholder="SFZRKMC" clearable>
            <el-option
              v-for="(item, index) in SFZRKMC_OPTION"
              :key="index"
              :label="item"
              :value="item">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="BZ" prop="BZ">
          <el-select v-model="searchForm.BZ" filterable placeholder="BZ" clearable>
            <el-option
              v-for="(item, index) in BZ_OPTION"
              :key="index"
              :label="item"
              :value="item">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="startXH" prop="startXH">
          <el-select v-model="searchForm.startXH" filterable placeholder="startXH" clearable>
            <el-option
              v-for="(item, index) in startXH_OPTION"
              :key="index"
              :label="item"
              :value="item">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="timeRange" prop="timeRange">
          <el-date-picker
            v-model="searchForm.timeRange"
            type="datetimerange"
            value-format="yyyy-MM-dd HH:mm:ss"
            range-separator="至"
            start-placeholder="开始日期"
            end-placeholder="结束日期">
          </el-date-picker>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="onSearch">查询</el-button>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="onReset">重置</el-button>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="onDownLoad">下载</el-button>
        </el-form-item>
      </el-form>
    </div>
    <el-table
      :data="tableData"
      stripe
      border
      height="calc(100% - 120px)"
      style="width: 100%"
    >
      <el-table-column
        prop="XH"
        label="XH">
      </el-table-column>
      <el-table-column
        prop="CX"
        label="CX">
      </el-table-column>
      <el-table-column
        prop="CP"
        label="CP">
      </el-table-column>
      <el-table-column
        prop="SFZRKMC"
        label="SFZRKMC">
      </el-table-column>
      <el-table-column
        prop="RKSJ"
        label="RKSJ">
      </el-table-column>
      <el-table-column
        prop="SFZCKMC"
        label="SFZCKMC">
      </el-table-column>
      <el-table-column
        prop="CKSJ"
        label="CKSJ">
      </el-table-column>
      <el-table-column
        prop="BZ"
        label="BZ">
      </el-table-column>
    </el-table>
    <div class="pagination">
      <el-pagination
        @current-change="handleCurrentChange"
        layout="total, prev, pager, next"
        :page-size="pageSize"
        :total="total">
      </el-pagination>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      CX_OPTION: [],
      SFZCKMC_OPTION: [],
      SFZRKMC_OPTION: [],
      BZ_OPTION: [],
      startXH_OPTION: [
        1000, 2000, 3000, 4000, 5000, 6000, 7000, 8000, 9000, 10000,
        11000, 12000, 13000, 14000, 15000, 16000, 17000, 18000, 19000, 20000,
      ],

      searchForm: {
        CX: "",
        SFZCKMC: "",
        SFZRKMC: "",
        BZ: "",
        startXH: "",
        timeRange: null,
      },

      rawData: [],  //原始数据
      tableData: [], //当前分页数据

      total: 0,
      pageSize: 100, //每页数
    }
  },
  created() {
    this.initTableData();
  },
  methods: {
    initTableData() {
      const params = { cond: {}, field: [], filetype: "xlsx" };
      const { CX, SFZCKMC, SFZRKMC, BZ, startXH, timeRange } = this.searchForm;
      if (CX) {
        params.cond.CX = CX;
      }
      if (SFZCKMC) {
        params.cond.SFZCKMC = SFZCKMC;
      }
      if (SFZRKMC) {
        params.cond.SFZRKMC = SFZRKMC;
      }
      if (BZ) {
        params.cond.BZ = BZ;
      }
      if (startXH) {
        params.cond.startXH = startXH;
      }
      if (timeRange && timeRange.length === 2) {
        params.cond.RKSJ = timeRange[0];
        params.cond.CKSJ = timeRange[1];
      }
      this.axios.post("/api/interactive", params).then((response) => {
        const { data } = response;
        //原始数据
        this.rawData = data.map(item => ({
          'XH': item[0],
          'CP': item[1],
          'CX': item[2],
          'SFZRKMC': item[3],
          'RKSJ': item[4],
          'SFZCKMC': item[5],
          'CKSJ': item[6],
          'BZ': item[7],
        }));
        this.total = data.length;
        //默认选择第一页
        this.handleCurrentChange(1)
        //下拉数据处理
        this.CX_OPTION = new Set(this.rawData.map(item => item.CX));
        this.SFZCKMC_OPTION = new Set(this.rawData.map(item => item.SFZCKMC));
        this.SFZRKMC_OPTION = new Set(this.rawData.map(item => item.SFZRKMC));
        this.BZ_OPTION = new Set(this.rawData.map(item => item.BZ));
      })
    },
    handleCurrentChange(val) {
      const startIndex = (val - 1) * this.pageSize;
      const endIndex = val  * this.pageSize;
      this.tableData = this.rawData.slice(
        startIndex, endIndex
      )
    },
    onSearch() {
      this.initTableData();
    },
    onReset() {
      this.$refs.form.resetFields();
      this.initTableData();
    },
    onDownLoad(command) {
      window.open("http://124.71.207.74:5000/download", "_blank");
    }
  }
}
</script>
