<template>
  <div class="Z39-1216-800">
    <div class="paintContainer" :style="{ width: width + 'px' }">
      <div class="paintTool">
        <div class="paintShape">
          <div
            :class="{active: item.name === radio2 }"
            v-for="(item, index) in paintShapeList"
            @click="radio2 = item.name"
            :key="index"
          >
            <el-tooltip effect="dark" :content="item.name" placement="top" :open-delay="500">
              <img :src="item.imgUrl" />
            </el-tooltip>
          </div>
        </div>
        <div class="inputNumber">
          <el-radio-group  size="mini" v-model="radio1">
            <el-radio-button label="填充"></el-radio-button>
            <el-radio-button label="轮廓"></el-radio-button>
          </el-radio-group>
          <div class="label">绘画样式</div>
        </div>
        <div class="inputNumber paintColor">
          <el-color-picker class="colorPicker" size="mini" v-model="color1"></el-color-picker>
          <div class="label">颜色</div>
        </div>
        <div class="inputNumber">
          <el-input-number size="mini" v-model="lineWidth" :min="1" :max="30"></el-input-number>
          <div class="label">画笔粗细</div>
        </div>
        <div class="inputNumber">
          <el-input-number size="mini" v-model="cornerRadius" :min="1" :max="30"></el-input-number>
          <div class="label">圆角矩形半径</div>
        </div>
        <div class="inputNumber">
          <el-input-number size="mini" v-model="polyNum" :min="3" :max="30"></el-input-number>
          <div class="label">多边形边数</div>
        </div>
      </div>
      <div class="paintArea">
        <el-card class="box-card">
          <div slot="header" class="header">
            <span>
              画板区域
              <i>（{{ width + " * "+ height}}）</i>
            </span>
            <el-button type="text" @click="setTheWaterMark">设置水印</el-button>
            <el-button type="text" @click="clearArtBoard">清空</el-button>
            <el-button type="text" @click="displayToImage">储存</el-button>
          </div>
          <canvas
            ref="paint"
            :width="width"
            @mousedown="onmousedown($event)"
          ></canvas>
        </el-card>
      </div>
    </div>
    <div class="imageContainer" :style="{ width: `calc(100% - ${width + 10}px)` }">
      <el-card class="saveImageArea">
        <div slot="header">
          <span>生成图片</span>
          <el-button style="float: right; padding: 3px 0" type="text" @click="clearToImage">清空</el-button>
        </div>
        <el-card v-for="(item, index) in imgBase60Data" class="saveImageCard">
          <el-image
            style="width: 100px; height: 100px"
            :src="item"
            :preview-src-list="[item]">
          </el-image>
          <i class="el-icon-delete" @click="deleteImage(index)"></i>
          <i class="el-icon-download" @click="saveToImage(item)"></i>
        </el-card>


        <el-statistic
          :value="deadline2"
          format="HH:mm:ss"
          time-indices
          title="剩余绘画时间"
          @finish="hilarity">
        </el-statistic>


      </el-card>
      <el-card class="importImageArea">
        <div slot="header">
          <span>上传图片预览</span>
          <el-upload
            class="uploadImage"
            action
            :http-request="httpRequest"
            :before-upload="beforeUploadClick"
            :limit="30"
            multiple
            :show-file-list="false">
            <el-button size="small" type="text">点击上传</el-button>
          </el-upload>
        </div>
        <el-card v-for="(item, index) in uploadImageBase64Data" class="uploadImageCard">
          <el-image
            style="width: 100px; height: 100px"
            :src="item.base64Url"
            :preview-src-list="[item.base64Url]">
          </el-image>
          <el-rate v-model="item.value"></el-rate>
        </el-card>
      </el-card>
    </div>
  </div>
</template>
<script>
import { asyncFileToBase64, asyncImage2Base64 } from "@/utils/files";
export default {
  data() {
    return {

      deadline2: Date.now() + 1000 * 60 * 1, //一分钟倒计时

      paintShapeList: [
        { name: "绘画", imgUrl: require("@/assets/huihua.svg") },
        { name: "直线", imgUrl: require("@/assets/line.svg") },
        { name: "矩形", imgUrl: require("@/assets/xingzhuang-juxing.svg") },
        { name: "圆角矩形", imgUrl: require("@/assets/yuanjiaojuxing1.svg") },
        { name: "多边形", imgUrl: require("@/assets/xingzhuang-xingxing.svg") },
        { name: "圆", imgUrl: require("@/assets/xingzhuang-tuoyuanxing.svg") },
        { name: "文本", imgUrl: require("@/assets/xingzhuang-wenzi.svg") },
      ],

      width: 800,
      height: 0,
      canvas: null,
      paint: null,
      paintImageData: {},

      radio1: "轮廓",
      radio2: "绘画",
      radio3: "纯色",
      color1: '#dc1b1b',
      color2: "#8493a3",

      lineWidth: 1, //线宽
      cornerRadius: 20, //圆角半径
      polyNum: 6, //多边形边数

      imgBase60Data: [],

      uploadImageBase64Data: [],
    }
  },
  mounted() {
    this.canvas = this.$refs.paint;
    this.paint = this.$refs.paint.getContext("2d");
    this.canvas.height = document.body.clientHeight - 208;
    this.height = document.body.clientHeight - 208;

    this.paint.beginPath();
    this.paint.fillStyle = "#fff";
    this.paint.fillRect(0, 0, this.canvas.width, this.canvas.height);
    this.paint.closePath();


    window.onresize = () => {
      this.canvas.height = document.body.clientHeight - 208;
      this.height = document.body.clientHeight - 208;
      this.paintImageData.hasOwnProperty("data") && this.paint.putImageData(this.paintImageData, 0, 0);
    }
  },
  methods: {
    onmousedown(e) {
      const { offsetX: x, offsetY: y } = e;
      if (this.radio2 === '绘画') {
        this.paint.clearRect(0, 0, this.width, this.height);
        this.paintImageData.hasOwnProperty("data") && this.paint.putImageData(this.paintImageData, 0, 0);
        this.paint.beginPath();
        this.paint.moveTo(x, y);
      }
      this.$refs.paint.onmousemove = (e) => {
        const { offsetX: x1, offsetY: y1 } = e;
        this.paint.clearRect(0, 0, this.width, this.height);
        this.paintImageData.hasOwnProperty("data") && this.paint.putImageData(this.paintImageData, 0, 0);
        switch (this.radio2) {
          case "绘画": this.paintMousePath(x1, y1); break;
          case "直线": this.paintLine(x, y, x1, y1); break;
          case "矩形": this.paintRectangle(x, y, x1, y1); break;
          case "圆角矩形": this.paintRoundedRectangle(x, y, x1, y1); break;
          case "多边形": this.paintPolygon(x, y, x1, y1); break;
          case "圆": this.paintCircle(x, y, x1, y1); break;
        }
      }
      this.$refs.paint.onmouseup = (e) => {
        this.$refs.paint.onmousemove = null;
        if (this.radio2 === "文本") {
          let input = window.prompt('请输入文字');
          if (input) {
            this.paintText(input, x, y);
          }
        }
        if (this.radio2 === '绘画') {
          this.paint.closePath();
        }
        this.paintImageData = this.paint.getImageData(0, 0, this.width, this.height);
      }
    },
    //绘制鼠标轨迹
    paintMousePath(x1, y1) {
      this.paint.lineTo(x1,y1);
      this.paint.lineWidth = this.lineWidth;
      this.paint.strokeStyle = this.color1;
      this.paint.stroke();
    },
    //绘制直线
    paintLine(x, y, x1, y1) {
      this.paint.beginPath();
      this.paint.moveTo(x,y);
      this.paint.lineTo(x1,y1);
      this.paint.lineWidth = this.lineWidth;
      this.paint.strokeStyle = this.color1;
      this.paint.stroke();
      this.paint.closePath();
    },
    //绘制矩形
    paintRectangle(x, y, x1, y1) {
      this.paint.beginPath();
      this.paint.lineWidth = this.lineWidth;
      switch(this.radio1){
        case '轮廓':
          this.paint.strokeStyle = this.color1;
          this.paint.strokeRect(x, y,x1-x,y1-y);
          break;
        case '填充':
          this.paint.fillStyle = this.color1;
          this.paint.fillRect(x, y,x1-x,y1-y);
          break;
      }
      this.paint.closePath();
    },
    //绘制圆角矩形
    paintRoundedRectangle(x, y, x1, y1) {
      this.paint.beginPath();
      this.paint.lineWidth = this.lineWidth;
      this.paint.moveTo(x + this.cornerRadius, y);
      this.paint.arcTo(x1, y, x1,y + this.cornerRadius, this.cornerRadius)
      this.paint.arcTo(x1, y1,x1 - this.cornerRadius, y1,this.cornerRadius)
      this.paint.arcTo(x, y1, x,y1 - this.cornerRadius, this.cornerRadius)
      this.paint.arcTo(x, y,x + this.cornerRadius, y, this.cornerRadius)
      this.paint.closePath();
      switch(this.radio1){
        case '轮廓':
          this.paint.strokeStyle = this.color1;
          this.paint.stroke();
          break;
        case '填充':
          this.paint.fillStyle = this.color1;
          this.paint.fill();
          break;
      }
    },
    //绘制多边形
    paintPolygon(x, y, x1, y1) {
      this.paint.beginPath();
      this.paint.lineWidth = this.lineWidth;
      let r = Math.sqrt(Math.pow(x1 - x,2) + Math.pow(y1 - y,2))
      let deg = 360 / this.polyNum * Math.PI/180;
      for(let i = 0; i < this.polyNum; i++){
        this.paint.lineTo(x + Math.cos(deg * i)*r,y + Math.sin(deg * i) * r)
      }
      this.paint.closePath();
      switch(this.radio1){
        case '轮廓':
          this.paint.strokeStyle = this.color1;
          this.paint.stroke();
          break;
        case '填充':
          this.paint.fillStyle = this.color1;
          this.paint.fill();
          break;
      }
    },
    //绘制圆
    paintCircle(x, y, x1, y1) {
      this.paint.beginPath();
      this.paint.lineWidth = this.lineWidth;
      let r = Math.sqrt(Math.pow(x1 - x,2) + Math.pow(y1 - y,2))
      this.paint.arc(x, y, r,0,Math.PI*2)
      this.paint.closePath();
      switch(this.radio1){
        case '轮廓':
          this.paint.strokeStyle = this.color1;
          this.paint.stroke();
          break;
        case '填充':
          this.paint.fillStyle = this.color1;
          this.paint.fill();
          break;
      }
    },
    paintText(input, x, y){
      this.paint.font='20px 宋体 bold';
      this.paint.textAlign='left';
      switch(this.radio1){
        case '轮廓':
          this.paint.strokeStyle = this.color1;
          this.paint.strokeText(input, x, y);
          break;
        case '填充':
          this.paint.fillStyle = this.color1;
          this.paint.fillText(input, x, y);
          break;
      }
    },
    //添加图片
    setTheWaterMark() {
      const img = new Image()
      img.src = 'data:image/png;base64,'
      img.onload = () => {
        const { width, height } = img;
        this.paint.beginPath();
        this.paint.fillStyle = this.paint.createPattern(img, 'repeat')
        this.paint.fillRect(this.width - width, this.height - height, this.width, this.height);
        this.paint.closePath();
        this.paintImageData = this.paint.getImageData(0, 0, this.width, this.height);
      }
    },
    clearArtBoard() {
      this.paint.clearRect(0, 0, this.width, this.height);
      this.paintImageData = {};
    },
    displayToImage() {
      if (!this.paintImageData.hasOwnProperty("data")) {
        return
      }
      if (this.imgBase60Data.length > 29) {
        return;
      }
      let url = this.canvas.toDataURL('image/jpeg');
      this.imgBase60Data.push(url)
    },
    clearToImage() {
      this.imgBase60Data = [];
    },
    //保存canvas画板为图片
    saveCanvasImage() {
      if (!this.paintImageData.hasOwnProperty("data")) {
        return
      }
      let url = this.canvas.toDataURL('image/jpg');
      let arr = url.split(","),
        mime = arr[0].match(/:(.*?);/)[1], // 此处得到的为文件类型
        bstr = atob(arr[1]), // 此处将base64解码
        n = bstr.length,
        u8arr = new Uint8Array(n);
      while (n--) {
        u8arr[n] = bstr.charCodeAt(n);
      }
      let file = new File([u8arr], "filename", { type: mime });
      let href = URL.createObjectURL(file); // 将file对象转成 UTF-16 字符串
      // 创建一个 a 标签
      let aLinkDom = document.createElement("a");
      aLinkDom.download = file.name; // 设置文件名
      aLinkDom.href = href; // 放入href
      document.body.appendChild(aLinkDom); // 将a标签插入 body
      aLinkDom.click(); // 触发 a 标签的点击
      document.body.removeChild(aLinkDom); // 移除刚才插入的 a 标签
      URL.revokeObjectURL(href); // 释放刚才生成的 UTF-16 字符串
    },
    deleteImage(index) {
      this.imgBase60Data.splice(index, 1);
    },
    saveToImage(url) {
      let arr = url.split(","),
        mime = arr[0].match(/:(.*?);/)[1], // 此处得到的为文件类型
        bstr = atob(arr[1]), // 此处将base64解码
        n = bstr.length,
        u8arr = new Uint8Array(n);
      while (n--) {
        u8arr[n] = bstr.charCodeAt(n);
      }
      let file = new File([u8arr], "filename", { type: mime });
      let href = URL.createObjectURL(file); // 将file对象转成 UTF-16 字符串
      // 创建一个 a 标签
      let aLinkDom = document.createElement("a");
      aLinkDom.download = file.name; // 设置文件名
      aLinkDom.href = href; // 放入href
      document.body.appendChild(aLinkDom); // 将a标签插入 body
      aLinkDom.click(); // 触发 a 标签的点击
      document.body.removeChild(aLinkDom); // 移除刚才插入的 a 标签
      URL.revokeObjectURL(href); // 释放刚才生成的 UTF-16 字符串
    },
    hilarity() {
      this.$notify({
        title: '提示',
        message: '时间已到，你可知寸金难买寸光阴？',
        duration: 0
      });
    },
    beforeUploadClick(file) {
      asyncFileToBase64(file).then(dataUrl => {
        this.uploadImageBase64Data.push({
          base64Url: dataUrl,
          value: null,
        });
      })
    },
    httpRequest(option) {},
  }
}
</script>
